import React, { Component } from "react";
import {TableBody, TableCell, TableHead, TableRow } from '@material-ui/core'
import { Button } from "@material-ui/core";
import { withStyles } from "@material-ui/core";
import { NavLink } from "react-router-dom";

const useStyles = (theme) => ({
  header: {
    background: "black"
  },
  tabs: {
    color: "white",
    fontSize: 17,
  },
  container: {
    width: '80%',
    margin: '3% 0% 0% 22%'
  },
  nav: {
    textDecoration: 'none'
  }
}
);
class Api extends Component {

  

  constructor() {
    super()
    this.state = {
      users: [],
      redirect: false
    }
    this.deleteUser = this.deleteUser.bind(this);
  }

    deleteUser(id)  {
      
    if(window.confirm('Are you sure?'))
      {
            fetch(`http://localhost:3003/users/${id}`,{
              method:'DELETE',
                header:{'Accept': 'application/json',
                'content-type': 'appication/json'
          }
    }
      )
    }
    window.location.reload(true);
  }
  
  componentDidMount() {
    const { classes } = this.props;
    fetch("http://localhost:3003/users")
      .then((resp) => resp.json())
      .then((data) => {
        let users = data.map((user, index) => {
          return (
            <TableRow>
              <TableCell>{user.id}</TableCell>
              <TableCell>{user.name}</TableCell>
              <TableCell>{user.username}</TableCell>
              <TableCell>{user.email}</TableCell>
              <TableCell>{user.phone}</TableCell>
              <TableCell>
                <NavLink to={`/edit/${user.id}`} className={classes.nav}><Button variant='contained' color='primary' style={{ marginRight: 10 }}>Edit</Button></NavLink>
                <Button variant="contained" color="secondary" onClick={() => this.deleteUser(user.id)}>Delete</Button>
              </TableCell>
            </TableRow> 
          );
        });
        this.setState({ users: users });
      });
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.container}>
        <TableHead>
          <TableRow className={classes.header}>
            <TableCell className={classes.tabs}>Id</TableCell>
            <TableCell className={classes.tabs}>Name</TableCell>
            <TableCell className={classes.tabs}>Username</TableCell>
            <TableCell className={classes.tabs}>Email</TableCell>
            <TableCell className={classes.tabs}>Phone</TableCell>
            <TableCell className={classes.tabs}>Actions</TableCell>
          </TableRow>
        </TableHead>
        <TableBody className={classes.tabs}>
          {this.data}
        </TableBody>
        {this.state.users}
      </div>
    );
  }
}
export default withStyles(useStyles)(Api);