import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
import { makeStyles, Button,Typography } from "@material-ui/core";
import { Grid, TextField } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    container: {
        width: '50%',
        margin: '5% 0% 0% 25%',
        '& > *': {
            marginTop: 20
        },
        fontSize: 40
    },
    tabs: {
        margin: '0% 0% 0% 25%',
        '& > *': {
            marginTop: 20
        },
        fontSize: 30,
        alignContent: 'center'
    },
    btn: {
        marginTop: 20,
        marginLeft: '50%',
        padding: '1% 42.5% 1% 42.5%',
        fontSize: 25,
        color: 'white'
    }
}
)
);


const EditUser = () => {
    const navigate = useNavigate();
    const { id } = useParams();
    const [user, setUser] = useState({
        name: "",
        username: "",
        email: "",
        phone: "",

    });

    const { name, username, email, phone } = user;
    const onInputChange = e => {
        setUser({ ...user, [e.target.name]: e.target.value });
    };

    useEffect(() => {
        loadUser();
    }, []);

    const add = async e => {
        e.preventDefault();
        await axios.put(`http://localhost:3003/users/${id}`, user);
        navigate('/all')
    };

    const loadUser = async () => {
        const result = await axios.get(`http://localhost:3003/users/${id}`);
        setUser(result.data);
    };

    const classes = useStyles();
    return (
        <>
            <Typography gutterBottom variant="h3" align="left" className={classes.container}>
                Update Detais :
            </Typography>
            <Grid xs={12} sm={6} item className={classes.tabs}>
                <TextField placeholder="Enter first name"
                    label="First Name" variant="outlined"
                    fullWidth required
                    name="name"
                    value={name}
                    onChange={e => onInputChange(e)}
                />
            </Grid>

            <Grid xs={12} sm={6} item className={classes.tabs}>
                <TextField placeholder="Enter user name"
                    label="@user_name" variant="outlined"
                    fullWidth required
                    name="username"
                    value={username}
                    onChange={e => onInputChange(e)}
                />
            </Grid>

            <Grid xs={12} sm={6} item className={classes.tabs}>
                <TextField placeholder="Enter Your E-mail Address"
                    label="Email Address" variant="outlined"
                    fullWidth required
                    name="email"
                    value={email}
                    onChange={e => onInputChange(e)}
                />
            </Grid>

            <Grid xs={12} sm={6} item className={classes.tabs}>
                <TextField placeholder="Enter Your Phone number"
                    label="Phone" variant="outlined"
                    fullWidth required
                    name="phone"
                    value={phone}
                    onChange={e => onInputChange(e)}
                />
            </Grid>
            <Grid xs={12} sm={6} item>
                <Button variant="contained" color="primary" className={classes.btn} onClick={e => add(e)}>Update</Button>
            </Grid>
        </>


    );
};

export default EditUser;