import React, { useState } from "react";
import axios from 'axios'
import { useNavigate } from "react-router-dom";
import { Button, FormControl, FormGroup, makeStyles, Typography } from "@material-ui/core";
import { TextField } from '@material-ui/core';
// import { useForm } from "react-hook-form";

const useStyles = makeStyles((theme) => ({

  container: {
    width: '50%',
    margin: '5% 0% 0% 25%',
    '& > *': {
      marginTop: 20
    },
    fontSize: 40
  },
  tabs: {
    width: '50%',
    margin: '0% 0% 0% 25%',
    '& > *': {
      marginTop: 20
    },
    fontSize: 30,
    alignContent: 'center'
  },
  btn: {
    marginLeft: '25%',
    width: '50%',
    marginTop: 20,
    fontSize: 25,
    color: 'white'
  }
}
)
);

const Aa = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState({
    name: "",
    username: "",
    email: "",
    phone: "",

  });

  const { name, username, email, phone } = user;
  const onInputChange = e => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const add = async e => {
    if (window.confirm('Thank You for Being our Valuable User')) {
      e.preventDefault();
      await axios.post("http://localhost:3003/users", user);
      navigate('/all')
    }
  };

  const classes = useStyles();
  return (
    <FormGroup>
      <Typography gutterBottom variant="h3" align="left" className={classes.container}>
        Add User :
      </Typography>
      <FormControl className={classes.tabs}>
          <TextField placeholder="Enter first name"
            label="First Name" variant="outlined"
            fullWidth required
            name="name"
            value={name}
            type="text"
            onChange={e => onInputChange(e)}
          />
      </FormControl>

      <FormControl className={classes.tabs}>
          <TextField placeholder="Enter user name"
            label="@user_name" variant="outlined"
            fullWidth required
            name="username"
            value={username}
            type="text"
            onChange={e => onInputChange(e)}
          />
      </FormControl>

      <FormControl className={classes.tabs}>
          <TextField placeholder="Enter Your E-mail Address"
            label="Email Address" variant="outlined"
            fullWidth required
            name="email"
            value={email}
            type="email"
            onChange={e => onInputChange(e)}
          />
      </FormControl>

      <FormControl className={classes.tabs}>
          <TextField placeholder="Enter Your Phone number"
            label="Phone" variant="outlined"
            fullWidth required
            name="phone"
            value={phone}
            type="number"
            onChange={e => onInputChange(e)}
          />
      </FormControl>

      <FormControl>
          <Button variant="contained" color="primary" className={classes.btn} onClick={e => add(e)}>Add</Button>
      </FormControl>
    </FormGroup>
  );

};

export default Aa;